<template>
  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-subheader>Bairro</v-subheader>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="bairro.id"
            label="Id"
            outlined
            disabled
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="bairro.nome"
            :counter="50"
            label="Nome"
            placeholder="Nome do Bairro"
            outlined
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col class="d-flex" cols="12">
          <v-combobox
          :items="cidades"
          :hint="`${bairro.cidade.nome + ' - ' + bairro.cidade.uf.sigla}`"
          v-model="bairro.cidade"
          item-text="nome"
          item-value="id"
          label="Cidades"
          placeholder="Escolha a Cidade"
          return-object
          outlined
          required></v-combobox>
        </v-col>
      </v-row>
      <v-btn color="success" class="" @click="save">Salvar</v-btn>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/BairroDataService";
import CidadeDataService from "../service/CidadeDataService";

export default {
  name: "Bairros",
  data() {
      return {
          bairro: {id:0,nome:'',cidade:{id:0,nome:'',uf:{id:0,nome:'',sigla:''}}},
          cidades: []
      };
  },
  computed: {
      id() {
          return this.$route.params.id;
      }
  },
  methods: {
      //textCombobox: uf => uf.sigla + ' â€” ' + uf.nome,
      refresh() {
          if(this.id > 0){
              DataService.retrieve(this.id)
                  .then(response => {
                  this.bairro = response.data
              });
          }
          CidadeDataService.retrieveAll()
            .then(response => {
            this.cidades = response.data
          })
      },
      save(e) {
          e.preventDefault();
          if(this.id > 0){
              DataService.update(this.id, this.bairro, {
                  id: this.id,
                  bairro: this.bairro
              }).then(() => {
                  this.$router.push('/bairros')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          } else {
              console.log(this.bairro);
              DataService.create(this.bairro, {
                  bairro: this.bairro
              }).then(() => {
                  this.$router.push('/bairros')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          }
      }
  },
  created() {
      this.refresh()
  }
};
</script>

